import Notiflix from 'notiflix';
import FilmsApiService from './fetchMainCards';
import { refs } from './refs.js';

import filmsCardTpl from '../hbs/sample-1.hbs';
import { genres } from '../js/genre';
import { openModalListener } from './modalCard.js';
import onTrailerClick from './trailer';

import { pagination } from './pagination/start-pagination';

// const DEBOUNCE_DELAY = 300;
let totalRenderedFilms = 0;
let page = 1;

const filmsApiService = new FilmsApiService();

refs.formNav.addEventListener('submit', onSearchFormSubmit);

function onSearchFormSubmit(e) {
  e.preventDefault();

  let value = e.currentTarget.elements.query.value;

  if (value.trim() === '') {
    return;
  }

  refs.filmsContainer.innerHTML = '';
  Notiflix.Loading.dots('Please wait...');

  filmsApiService
    .fetchCards(page, value)
    .then(addFilmsCardMarkup)
    .then(pagination)
    .catch(error => {
      console.log(error);
    });
}

export function addFilmsCardMarkup(data) {
  const totalPages = data.total_pages;
  refs.filmsContainer.setAttribute('dataPage', totalPages);

  Notiflix.Loading.remove();
  if (data.results.length === 0) {
    Notiflix.Notify.failure(
      'Sorry, there are no films matching your search query. Please try again.',
    );
    return;
  }

  totalRenderedFilms = data.results.length * data.total_pages;
  addGenres(data.results);
  addPoster(data.results);
  addDate(data.results);

  const collectionPopFilm = data.results.map(result => {
    return {
      id: result.id,
      vote_average: result.vote_average,
      poster_path: result.poster_path,
      overview: result.overview,
      title: result.title,
      genre_ids: result.genre_ids,
      release_date: result.release_date.split('-')[0],
    };
  });

  const markup = filmsCardTpl(collectionPopFilm);
  refs.filmsContainer.innerHTML = markup;

  openModalListener();
  onTrailerClick();
  Notiflix.Notify.success(`We found ${totalRenderedFilms} films for you.`);
  //   const refsPagin = document.querySelector('#root_futer');
  //   refsPagin.innerHTML = '';
  // onInputChange();
  // themeAfterPageReload();
}

// Подмена id на имя жанра и обрезка по длине строки
function addGenres(results) {
  const arr = results.map(genre => {
    return genre.genre_ids;
  });

  const newArr = arr.map(el => {
    return el.map(id => {
      const x = genres.find(gen => gen.id === id);
      return (id = x.name);
    });
  });

  const genresName = newArr.map(id => {
    if (id.length === 0) {
      return (id = 'Something interesting');
    }

    if (id.length === 1) {
      return id;
    }

    if (id.length === 2) {
      return `${id[0]},  ${id[1]}`;
    }

    if (id.length > 2) {
      return `${id[0]},  ${id[1]},  Other`;
    }
  });

  let index = 0;
  results.forEach(el => {
    el.genre_ids = genresName[index];
    index += 1;
  });
}

// добавление постера в свойство poster_path
function addPoster(results) {
  results.forEach(el => {
    if (el.poster_path === null) {
      el.poster_path =
        'data:image/jpeg;base64,';
    } else el.poster_path = `https://image.tmdb.org/t/p/w500${el.poster_path}`;
  });
}

// добавление даты, если ее нет
function addDate(results) {
  results.forEach(el => {
    if (el.release_date === undefined || el.release_date === '') {
      el.release_date = 'new here';
    }
    return;
  });
}

Notiflix.Loading.init({ svgColor: '#ff6b08', messageColor: '#ff6b08' });

Notiflix.Notify.init({
  success: {
    background: 'rgba(255, 107, 8, 0.8)',
    notiflixIconColor: 'rgba(0,0,0,0.4)',
  },
});
